export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "about-message": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["This is my final web-programming lab."])},
        "powered": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Powered by Vue3."])},
        "about-me": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Kamil Chulakov"])}
      },
      "ru": {
        "about-message": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Это лаба по веб-программированию."])},
        "powered": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Работает на Vue3."])},
        "about-me": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Камиль Чулаков"])}
      }
    }
  })
}
