export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "about-message": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Nice to see you here."])},
        "about-me": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Kamil Chulakov"])}
      },
      "ru": {
        "about-message": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Гм… Всего знать нельзя, конечно… Как бы обширен ум ни был,\n всего туда не поместишь. Ты ведь все больше насчет философии?"])},
        "about-me": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["А.П. Чехов"])}
      }
    }
  })
}
